  font-family: Pacifico, "IBM Plex Sans", sans-serif, Verdana;
  border-bottom: 2px solid #868683;
  padding-bottom: 4px;
  padding-left: 20px;
  color: #585753;
  margin-right: 30px;
  margin-bottom: 5px;
}

#portfolio-displaymode > #portfolio-dm-header > img {
  float: right;
  height: 25px;
