  background-color: white;
  color: #403f3d;
  font-weight: bold;
}

#portfolio-displaymode
  > #portfolio-displaymode-body
  > #portfolio-dm-row
  > div
  > .enabled {
  background-color: #49ff58;
}

#portfolio-displaymode
  > #portfolio-displaymode-body
  > #portfolio-dm-row
  > div
  > .enabled:disabled,
#portfolio-displaymode
  > #portfolio-displaymode-body
  > #portfolio-dm-row
  > div
  > .enabled:disabled:hover {
  background-color: #85d88c;
}
</style>
