<template>
  <div id="portfolio-hierarchy">
    <div id="portfolio-hierarchy-header">
      Hierarchy
      <img src="@/assets/collapse.png" v-if="!collapse" @click="collapse = true">
      <img src="@/assets/expand.png" v-if="collapse" @click="collapse = false">
      <button
        :class=" edit ? 'save' : '' "
        @click="edit = !edit, collapse = false"
      >{{ edit ? 'Save' : 'Edit'}}</button>
    </div>
    <span id="portfolio-hierarchy-body" v-show="!collapse">
      <div id="portfolio-hierarchy-row-head">
        <div>Technical ID</div>
        <div>Display Name</div>
        <div>Position</div>
        <div>Status</div>
      </div>
      <div id="portfolio-hierarchy-row" v-for="(item, index) in hierarchies" :key="index">
        <div>{{item.hierarchyId}}</div>
        <div>{{item.displayName}}</div>
        <div>
          <input
            :placeholder="item.defaultPosition"
            v-model="item.userSelPosition"
            :readonly="!edit"
          >
        </div>
        <div>
          <button
            :disabled="!edit"
            v-show="item.userSelected === 0"
            @click="item.userSelected = 1"
          >{{ 'Enable' }}</button>
          <button
            :disabled="!edit"
            class="enabled"
            v-show="item.userSelected === 1"
            @click="item.userSelected = 0"
          >{{ 'Disable' }}</button>
        </div>
      </div>
    </span>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "portfolio-hierarchy",
  components: {},
  props: {
    clientid: Number,
    portfolioid: String
  },
  data() {
    return {
      edit: false,
      collapse: false,
      hierarchies: []
    };
  },

  watch: {},
  methods: {},
  created() {
    https
      .get(
        `/api/configuration/portfolio.xsjs?type=hierarchy&clientid=${
          this.clientid
        }&portfolio=${this.portfolioid}`
      )
      .then(
        resolve => {
          this.hierarchies = resolve.data;
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching Hierarchies",
            message: error
          });
        }
      );
  }
};
</script>

