import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    logedin: false, //TODO: Change it to false after implementation, false is default
    hosts: [
      {
        id: "DEV",
        host: "https://im1a7641aced.hana.ondemand.com/mr/mind/master/common"
      },
      {
        id: "INT",
        host: "https://im0a2cfe618e.hana.ondemand.com/mr/mind/master/common"
      },
      {
        id: "PRD",
        host: "https://ilza5bf95118.hana.ondemand.com/mr/mind/master/common"
      }
    ]
  },
  getters: {
    isLogedin: state => {
      return state.logedin;
    },
    getSysAddr: state => sys => {
      return state.hosts.find(env => env.id === sys.toUpperCase());
    }
  },
  mutations: {
    isAuthenticated(state, logedin) {
      state.logedin = logedin;
    }
  },
  actions: {
    isAuthenticated(context, logedin) {
      return new Promise(resolve => {
        context.commit("isAuthenticated", logedin);
        resolve(this.getters.isLogedin);
      });
    }
  }
});
