import Vue from "vue";
import VueRouter from "vue-router";
import HazardLayer from "@/views/HazardLayer.vue";
import ClientManagement from "@/views/ClientManagement.vue";
import Dashboard from "@/views/Dashboard.vue";
import Portfolio from "@/views/Portfolio.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard
  },
  {
    path: "/hazardmapping",
    name: "HazardLayerMapping",
    component: HazardLayer
  },
  {
    path: "/crediantialmgmt",
    name: "ClientManagement",
    component: ClientManagement
  },
  {
    path: "/portfolio",
    name: "Portfolio Selection",
    component: Portfolio
  }
];

// eslint-disable-next-line no-new
const router = new VueRouter({
  mode: "abstract",
  routes: routes
});

